___CSS_LOADER_EXPORT___.push([module.id, "\n.digivigilMainDiv {\n  display: block;\n  position: relative;\n\n  text-align: center;\n  color: #205377;\n  overflow: visible;\n}\n#MainInfoDiv {\n  line-height: 18pt;\n  display: block;\n\n  text-align: left;\n\n  background: rgba(96, 95, 95, 0.3);\n  padding: 20px;\n\n  margin: auto;\n  font-size: 18pt;\n  white-space: normal;\n\n  display: inline-block;\n\n  text-align: center;\n  padding: 20px;\n  width: 100%;\n}\n.MainYouTubeDiv {\n  display: inline-block;\n  margin: auto;\n  margin-bottom: 40px;\n  margin-top: 20px;\n}\n.MainYoutubeVideoIframe {\n  display: block;\n  border: solid white 5px;\n  border-radius: 20px;\n  width: 80vw;\n  height: 45vw;\n}\n.MainRouterLink {\n  text-decoration: none;\n  font-weight: bold;\n  color: #205377;\n}\n", "",{"version":3,"sources":["webpack://./src/components/digivigil/progression/Main.vue"],"names":[],"mappings":";AAoFA;EACE,cAAc;EACd,kBAAkB;;EAElB,kBAAkB;EAClB,cAAc;EACd,iBAAiB;AACnB;AAEA;EACE,iBAAiB;EACjB,cAAc;;EAEd,gBAAgB;;EAEhB,iCAAiC;EACjC,aAAa;;EAEb,YAAY;EACZ,eAAe;EACf,mBAAmB;;EAEnB,qBAAqB;;EAErB,kBAAkB;EAClB,aAAa;EACb,WAAW;AACb;AAEA;EACE,qBAAqB;EACrB,YAAY;EACZ,mBAAmB;EACnB,gBAAgB;AAClB;AAEA;EACE,cAAc;EACd,uBAAuB;EACvB,mBAAmB;EACnB,WAAW;EACX,YAAY;AACd;AACA;EACE,qBAAqB;EACrB,iBAAiB;EACjB,cAAc;AAChB","sourcesContent":["<template>\n  <div class=\"MainInfoDiv\">\n    <br />\n    <div\n      class=\"MainInfoSubTitleDiv\"\n      style=\"\n        margin-bottom: 40px;\n        border: none;\n        border-bottom: solid black 4px;\n        text-align: left;\n      \"\n    >\n      State of Progression:\n    </div>\n    <div class=\"MainInfoDiv\">\n      We are currently in a stage of transition from a prototype to pre-alpha\n      release of Balek and Diaplode. Our goal is to create high-quality\n      offerings that are suitable for public adoption.\n      <br />\n      <br />\n      To achieve this goal, we have identified several key areas that require\n      attention, including refactoring from JavaScript to TypeScript, updating\n      documentation, and creating unit tests. The current iterations of Balek\n      and Diaplode utilize the Dojo Toolkit to facilitate object-oriented\n      programming (OOP) design patterns and concepts, transpiling, and\n      minification. However, as newer language features and packages like ES6,\n      TypeScript, Webpack, and Babel have emerged and matured, the relevance of\n      the Dojo Toolkit has diminished in modern software development. As a\n      result, we are transitioning to TypeScript and using modern tools to\n      streamline our development process.\n      <br />\n      <br />\n      As the TypeScript conversion occurs, we will provide updated\n      <a class=\"MainLink\" href=\"/docs/\">documentation</a>,\n      <a class=\"MainLink\" href=\"/docs/jest-stare/\">tests</a>, and\n      <a class=\"MainLink\" href=\"/docs/coverage/\">test coverage</a> reflecting\n      the current state of the projects. We want to ensure that our software is\n      reliable, efficient, and user-friendly. Our vision is to create software\n      that empowers users and makes knowledge available, obtainable, and free.\n      <br />\n      <br />\n    </div>\n\n    <br />\n    <div\n      class=\"MainInfoSubTitleDiv\"\n      style=\"\n        margin-bottom: 40px;\n        border: none;\n        border-bottom: solid black 4px;\n        text-align: left;\n      \"\n    >\n      Future:\n    </div>\n    <div class=\"MainInfoDiv\">\n      CI/CD workflows and release strategies will be tackled as a priority after\n      the initial pre-alpha release to prevent user issues as development\n      progresses.\n      <br />\n      <br />\n      Our long-term goal is to build a community around Balek and Diaplode, with\n      contributors and users from around the world. We believe that by\n      prioritizing quality and user experience, we can create software that\n      people want to use and that helps them achieve their goals. We welcome you\n      to keep track of our progress on GitHub and join us in our efforts to\n      enhance the future with Balek and Diaplode.\n    </div>\n  </div>\n</template>\n<script>\nimport BalekInfo from \"../../balek/Info.vue\";\nimport DiaplodeInfo from \"../../diaplode/Info.vue\";\n\nexport default {\n  name: \"DigivigilProgression\",\n  data: function () {},\n  components: {\n    BalekInfo,\n    DiaplodeInfo,\n  },\n};\n</script>\n<style>\n.digivigilMainDiv {\n  display: block;\n  position: relative;\n\n  text-align: center;\n  color: #205377;\n  overflow: visible;\n}\n\n#MainInfoDiv {\n  line-height: 18pt;\n  display: block;\n\n  text-align: left;\n\n  background: rgba(96, 95, 95, 0.3);\n  padding: 20px;\n\n  margin: auto;\n  font-size: 18pt;\n  white-space: normal;\n\n  display: inline-block;\n\n  text-align: center;\n  padding: 20px;\n  width: 100%;\n}\n\n.MainYouTubeDiv {\n  display: inline-block;\n  margin: auto;\n  margin-bottom: 40px;\n  margin-top: 20px;\n}\n\n.MainYoutubeVideoIframe {\n  display: block;\n  border: solid white 5px;\n  border-radius: 20px;\n  width: 80vw;\n  height: 45vw;\n}\n.MainRouterLink {\n  text-decoration: none;\n  font-weight: bold;\n  color: #205377;\n}\n</style>\n"],"sourceRoot":""}]);
